.wrap {
  height: 100vh;

  width: 100%;

  display: flex;

  background-image: url("../images/background.jpg");
  background-color: rgba(2, 2, 2, 0.1);
  align-items: center;
  background-position: 50% 30%;
  background-repeat: no-repeat;
  background-size: cover;
}
.already {
  position: absolute;
  top: 92%;
  align-self: center;
}
.ventures {
  position: absolute;
  top: 24.8%;
  left: 20px;
  font-size: 8.25em;
  font: Bariol;
  letter-spacing: 0px;
  color: #707070;
  opacity: 1;
  font-stretch: expanded;
}

.error {
  border: 1px solid red;
}

.errorMessage {
  color: red;

  font-size: 0.75em;

  display: relative;
}

.root {
  position: absolute;
  right: 10%;
  width: 30%;
  bottom: 85px;
  display: flex;
  vertical-align: middle;
  flex-direction: column;
  height: 69%;
  padding: 8px 20px;
  border-radius: 20px;
  border: 3px solid #f93800;

  background-color: #ffffff;
}

.username {
  background-color: #ffffff;
  position: absolute;
  top: 25%;
  width: 85%;
  display: flex;
  flex-direction: column;
}
.useremail {
  background-color: #ffffff;
  position: absolute;
  top: 38%;
  width: 85%;
  display: flex;
  flex-direction: column;
}
.userpassword {
  background-color: #ffffff;
  position: absolute;
  width: 85%;
  display: flex;
  flex-direction: column;
  top: 53%;
}
.userconpassword {
  background-color: #ffffff;
  position: absolute;
  width: 85%;
  display: flex;
  flex-direction: column;
  top: 68%;
}
.forgotPass {
  position: absolute;
  top: 83%;
  align-self: center;
}

.log {
  align-self: flex-end;
}
.reg button {
  width: 180px;
  height: 40px;
  position: absolute;
  top: 84%;
  right: 46px;
  border-radius: 10px;
}

.logintext {
  width: 104px;
  height: 100px;
  position: absolute;
  right: 39%;
  font-weight: lighter;
  top: 15px;
  font-size: 34px;
  font: Regular Bariol;
  letter-spacing: 0px;
  color: #707070;
  opacity: 1;
}

.reg button:hover {
  color: #519e8a;

  background-color: #fff;

  border: 2px solid #519e8a;
}

.image {
  align-self: center;
  position: absolute;
  top: 10px;
  width: 31px;
  height: 34px;
}
.logo {
  position: absolute;
  top: 10px;
  left: -12px;
  width: 300px;
